<template lang='pug'>
    div.selector
        .select-input
            input(@click='show_toggle', :value='value', :placeholder="placeholder")
        el-collapse-transition
            .select-card(v-show='toggle', @click='stop_prop')
                el-card(style='width: 570px')
                    el-tabs(v-model='activeName', type='card')
                        el-tab-pane(label='国内热门', name='hot')
                            .city-list
                                ul.list
                                    li.city_li(v-for='item in hot', @click='city_select(item)') {{item.cityNameCN}}
                        el-tab-pane(label='ABCDE', name='abcde')
                            .city-list
                                p.title A
                                ul.list
                                    li(v-for='item in city_a', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title B
                                ul.list
                                    li(v-for='item in city_b', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title C
                                ul.list
                                    li(v-for='item in city_c', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title D
                                ul.list
                                    li(v-for='item in city_d', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title E
                                ul.list
                                    li(v-for='item in city_e', @click='city_select(item)') {{item.cityNameCN}}

                        el-tab-pane(label='FGHJ', name='fghj')
                            .city-list
                                p.title F
                                ul.list
                                    li(v-for='item in city_f', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title G
                                ul.list
                                    li(v-for='item in city_g', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title H
                                ul.list
                                    li(v-for='item in city_h', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title J
                                ul.list
                                    li(v-for='item in city_j', @click='city_select(item)') {{item.cityNameCN}}
                        el-tab-pane(label='KLMNP', name='klmnp')
                            .city-list
                                p.title K
                                ul.list
                                    li(v-for='item in city_k', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title L
                                ul.list
                                    li(v-for='item in city_l', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title M
                                ul.list
                                    li(v-for='item in city_m', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title N
                                ul.list
                                    li(v-for='item in city_n', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title P
                                ul.list
                                    li(v-for='item in city_p', @click='city_select(item)') {{item.cityNameCN}}
                        el-tab-pane(label='QRSTW', name='qrstw')
                            .city-list
                                p.title Q
                                ul.list
                                    li(v-for='item in city_q', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title R
                                ul.list
                                    li(v-for='item in city_r', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title S
                                ul.list
                                    li(v-for='item in city_s', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title T
                                ul.list
                                    li(v-for='item in city_t', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title W
                                ul.list
                                    li(v-for='item in city_w', @click='city_select(item)') {{item.cityNameCN}}
                        el-tab-pane(label='XYZ', name='xyz')
                            .city-list
                                p.title X
                                ul.list
                                    li(v-for='item in city_x', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title Y
                                ul.list
                                    li(v-for='item in city_y', @click='city_select(item)') {{item.cityNameCN}}
                            .city-list
                                p.title Z
                                ul.list
                                    li(v-for='item in city_z', @click='city_select(item)') {{item.cityNameCN}}
                    .city__close(@click='toggle=false')
                        i.el-icon-close
</template>

<script>
export default {
    data () {
        return {
            activeName: 'hot',
            city_chose: {},
            toggle: false,
            citys: [],
            hot: [],
            city_a: [],
            city_b: [],
            city_c: [],
            city_d: [],
            city_e: [],
            city_f: [],
            city_g: [],
            city_h: [],
            city_i: [],
            city_j: [],
            city_k: [],
            city_l: [],
            city_m: [],
            city_n: [],
            city_o: [],
            city_p: [],
            city_q: [],
            city_r: [],
            city_s: [],
            city_t: [],
            city_u: [],
            city_v: [],
            city_w: [],
            city_x: [],
            city_y: [],
            city_z: []
        }
    },
    props: {
        placeholder: '',
        value: ''
    },
    watch: {
        city_chose () {
            // this.value = this.city_chose.cityNameCN + '(' + this.city_chose.cityCode + ')'
        }
    },
    methods: {
        show_toggle () {
            this.toggle = !this.toggle
            event.stopPropagation()
            event.preventDefault()
        },
        stop_prop () {
            event.stopPropagation()
        },
        city_select (item) {
            this.city_chose = item
            this.toggle = false
            this.$emit('itemSelect', item)
            this.$emit('input', item.cityNameCN + '(' + item.cityCode + ')')
        }
    },
    created () {
        let _this = this
        this.$http.get('../../../static/city.json', {
            local_data: true
        }).then(function (response) {
            _this.citys = response.data
            for (let i = 0; i < _this.citys.length; i++) {
                if (_this.citys[i].isHot === 'Y') {
                    _this.hot.push(_this.citys[i])
                }
                let fPy = _this.citys[i].cityPy.substr(0, 1)
                switch (fPy) {
                case 'a': _this.city_a.push(_this.citys[i])
                    break
                case 'b': _this.city_b.push(_this.citys[i])
                    break
                case 'c': _this.city_c.push(_this.citys[i])
                    break
                case 'd': _this.city_d.push(_this.citys[i])
                    break
                case 'e': _this.city_e.push(_this.citys[i])
                    break
                case 'f': _this.city_f.push(_this.citys[i])
                    break
                case 'g': _this.city_g.push(_this.citys[i])
                    break
                case 'h': _this.city_h.push(_this.citys[i])
                    break
                case 'i': _this.city_i.push(_this.citys[i])
                    break
                case 'j': _this.city_j.push(_this.citys[i])
                    break
                case 'k': _this.city_k.push(_this.citys[i])
                    break
                case 'l': _this.city_l.push(_this.citys[i])
                    break
                case 'm': _this.city_m.push(_this.citys[i])
                    break
                case 'n': _this.city_n.push(_this.citys[i])
                    break
                case 'o': _this.city_o.push(_this.citys[i])
                    break
                case 'p': _this.city_p.push(_this.citys[i])
                    break
                case 'q': _this.city_q.push(_this.citys[i])
                    break
                case 'r': _this.city_r.push(_this.citys[i])
                    break
                case 's': _this.city_s.push(_this.citys[i])
                    break
                case 't': _this.city_t.push(_this.citys[i])
                    break
                case 'u': _this.city_u.push(_this.citys[i])
                    break
                case 'v': _this.city_v.push(_this.citys[i])
                    break
                case 'w': _this.city_w.push(_this.citys[i])
                    break
                case 'x': _this.city_x.push(_this.citys[i])
                    break
                case 'y': _this.city_y.push(_this.citys[i])
                    break
                case 'z': _this.city_z.push(_this.citys[i])
                    break
                default: alert('default')
                }
            }
        }).catch(function (error) {
            console.log(error)
        })
        document.querySelector('body').addEventListener('click', function () {
            _this.toggle = false
        }, false)
    }
}
</script>

<style scoped>
    .selector {
        position: relative;
        z-index: 20;
    }
    .select-card {
        position: absolute;
        margin-top: 3px;
        z-index: 10001;
    }
    input {
        width: 100%;
        border-radius: 4px;
        height: 40px;
        line-height: 1.5;
        padding: 0 15px;
        box-sizing: border-box;
        border: 1px solid #dcdfe6;
        background-color: #fff;
        color: #606266;
        line-height: 1;
        outline: 0;
        font-size: inherit;
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    }
    input:focus {
        border-color: #409EFF;
        outline: 0;
    }
    .city-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
        position: relative;
        border-bottom: 1px solid #dfdfdf;
    }
    .city-list > .title {
        position: absolute;
        font-size: 20px;
        color: red;
    }
    .city-list > ul.list {
        margin-left: 30px;
        font-size: 12px;
        display: flex;
        flex-wrap: wrap;
    }
    .city-list > ul > li {
        /* width: 110px; */
        width: 90px;
        display: inline-block;
    }
    .city-list > ul > li:hover {
        color: #2d8cf0;
        cursor: pointer;
    }
    .city__close {
        position: absolute;
        top: 0;
        right: 12px;
        cursor: pointer;
    }

</style>



